<template>
    <div class="my1">
      <div class="my1_login">
        <img style="border-radius:50%;" v-lazy="'http://127.0.0.1:3000'+uimg" alt="">
        <p>Hello:</p>
        <p style="color:#0f0;" v-text="`${uname}`"></p>
      </div>
      <div class="my1_p">
        <div>
          <span>活动中心</span>
          <p>参与投票赢大奖</p>
        </div>
        <div>
          <span>会员中心</span>
          <p>抢票看玩具总动员</p>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      uname:"",
      uimg:"",
      myImgurl:"http://lorempixel.com/400/200/"
    };
  },
  methods: {
    get() {
      // 用localstorage保存用户输入的id;
      var id = window.localStorage.getItem("id");
      var params = {id};
      this.axios.get("/get",{params}).then(res => {
        let data = res.data[0];
        this.uname= data.uname;
        this.uimg=data.uimg;
      });
    }
  },
  created() {
    this.get();
  }
};
</script>

<style scoped>
img[lazy=loading] {
  width: 100%;
  height: 100px;
  margin: auto;
}
.my1_login{
  height:400rpx;
  text-align: center;
  position: relative;
  width:100%;
  display: flex;
  flex-direction: column;
}
.my1_login>img{
  width: 30%;
  margin-left:35%;
}
.my1_login>p{
  font-size:1.8rem;
  margin-top:0.5rem;
  color:#aaa;
}
.my1_p{
  display: flex;
  justify-content:center;
}
.my1_p>div{
  padding:2rem;
}
</style>
