<template>
    <div class="my3">
        <div class="my3_img">
            <h2 style="color:#666;">自建歌单</h2><span style="font-size:1.8rem;margin:0.6rem 0 0 1rem;color:#0f0;">{{$store.getters.opn}}</span>
            <div class="my3_c">
                <div class="img" @click="add">
                    <img style="width:100%;" src="../../../assets/icon/add.png" alt="">
                </div>
                <div class="img" @click="ajj">
                    <img style="width:100%;"  src="../../../assets/icon/guanli.png" alt="">
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
  data() {
    return {
      // 这是上个组件传的id;但是这里我没有用到
      n: this.$route.query.lid,//写0也没关系,
    };
  },
  watch:{
  },
  methods: {
    add() {
      var lid = this.n;
      var url = "/addcart";
      var params = { lid };
      this.axios.get(url, { params }).then(result => {
        // 如果已经建立了一个歌单
        this.$store.commit("addn");
      });
    },
    ajj() {
      var lid = this.n;
      var url = "/addcart";
      var params = { lid };
      this.axios.get(url, { params }).then(result => {
        // commit用来调用mutations中的方法
        this.$store.commit("ajjn");
      });
    }
  }
};
</script>

<style scoped>
.my3{
  background:rgb(221, 216, 216);
}
.my3_img {
  display: flex;
  padding: .5rem;
}
div.my3_c {
  display: flex;
  justify-content: flex-end;
  flex: 1;
}
.img {
  width: 15%;
  margin-right: 0.5rem;
}

</style>
